<template>
  <div class="m-pager">
    <div class="btn prev-btn"
         :class="{disable: currPage === 1}"
         @click="prevPage"></div>
    <ul class="nums-btn-wrapper">
      <li class="btn num-btn"
          :class="{current: currPage === startPage + i}"
          @click="changePageNum(startPage + i)"
          v-for="i in btnCount" :key="i">{{startPage + i}}</li>
    </ul>
    <div class="btn next-btn"
         :class="{disable: currPage === pageCount}"
         @click="nextPage"></div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    name: 'Pager',
    props: {
      // 
      pageSize: {
        type: Number,
        default: 1
      },
      // 
      totalCount: {
        type: Number,
        default: 0
      },
      // 
      maxBtnCount: {
        type: Number,
        default: 12
      }
    },
    data () {
      return {
        // 
        currPage: 1,
        // 
        startPage: 0
      }
    },
    watch: {
      /*  */
      totalCount () {
        this.startPage = 0
        this.currPage = 1
      },
      /*    */
      currPage (newPage, oldPage) {
        console.log(newPage, oldPage)
        this.$emit('pageChange', newPage)
        if (this.pageCount > this.maxBtnCount) {
          this.alignPageCenter()
        }
      }
    },
    computed: {
      /*  */
      pageCount () {
        return Math.ceil(this.totalCount / this.pageSize)
      },
      /*  */
      btnCount () {
        if (this.pageCount > this.maxBtnCount) {
          this.computePageCenter()
          return this.maxBtnCount
        } else {
          return this.pageCount
        }
      }
    },
    methods: {
      /*  */
      computePageCenter () {
        let num = this.maxBtnCount - 1
        this.pageCenter = {
          // 
          leftNum: Math.floor(num / 2),
          // 
          rightNum: Math.ceil(num / 2)
        }
      },
      /*  */
      alignPageCenter () {
        // this.maxBtnCount
        let firstNum = this.currPage - this.pageCenter.leftNum
        // this.maxBtnCount
        let lastNum = this.currPage + this.pageCenter.rightNum
        if (firstNum <= 1) {
          //  0
          this.startPage = 0
        } else if (firstNum > 1 && lastNum <= this.pageCount) {
          this.startPage = firstNum - 1
        } else if (lastNum > this.pageCount) {
          this.startPage = this.pageCount - this.maxBtnCount
        }
      },
      /*  */
      changePageNum (num) {
        this.currPage = num
      },
      /*  */
      prevPage () {
        let page = this.currPage
        if (page <= 1) {
          return
        }
        //  this.currPage  1
        this.currPage = Math.max(1, --page)
      },
      /*  */
      nextPage () {
        let page = this.currPage
        if (page >= this.pageCount) {
          return
        }
        //  this.currPage  this.pageCount
        this.currPage = Math.min(this.pageCount, ++page)
      }
    }
  }
</script>

<style lang="less" scoped rel="stylesheet/less">
  @import "~common/less/variable";

  .m-pager {
    display: inline-block;
    .btn {
      width: 26px;
      height: 26px;
      font-size: @font-size-small;
      line-height: 26px;
      text-align: center;
      cursor: pointer;
    }
    .prev-btn {
      float: left;
      margin-right: 20px;
      background: url("./prev.png") no-repeat center center;
      &.disable {
        cursor: default;
      }
    }
    .nums-btn-wrapper {
      float: left;
      font-size: 0;
      .num-btn {
        display: inline-block;
        margin-right: 8px;
        color: @color-text-ll;
        &:last-child {
          margin-right: 0;
        }
        &.current {
          cursor: default;
        }
        &.current, &:hover {
          color: @color-theme;
        }
      }
    }
    .next-btn {
      float: left;
      margin-left: 20px;
      background: url("./next.png") no-repeat center center;
      &.disable {
        cursor: default;
      }
    }
  }
</style>