  @import "~common/less/variable";

  .mv-detail {
    padding: 15px 0 60px;
    background: @color-background;
    /*  */
    &.video-fullscreen {
      height: 0;
      overflow: hidden;
    }
    .mv {
      height: 566px;
    }
    .intro {
      padding: 0 60px 35px;
      font-size: @font-size-small;
      line-height: 24px;
      word-break: break-all;
      word-wrap: break-word;
      white-space: pre-line;
      color: @color-text-dd;
    }
  }
</style>
